
import React, { Component } from 'react'
import GameService from '../../services/gameService';
import UtilService from '../../services/utilService';
import './rollDice.css'
import Die from '../die/die'
import Modal from '../modal/modal'

class RollDice extends Component {



  static defaultProps = {
    sides: ['one', 'two', 'three',
      'four', 'five', 'six']
  }

  constructor(props) {
    super(props)

    this.state = {
      die: 'one',
      rolling: false,
      isOpen: false,
      msg: ''
    }
    this.OnRoll = this.OnRoll.bind(this)
  }

  OnRoll() {
    const { sides } = this.props
    this.setState({
      die: sides[Math.floor(Math.random() * sides.length)],
      rolling: true
    })

    setTimeout(() => {
      this.setState({ rolling: false })
    }, 1000)

    console.log('state', this.state.die);

    switch (this.state.die) {

      case 'one':
        GameService.roll(1);
        GameService.saveLog(1,Date.now());
        this.showMsg('Game over!')
        break;

      case 'two':
        GameService.roll(2);
        let num = this.getRandomNum()
        console.log(num);
        if (num <= 0.5) {
          this.showMsg(' The rum spoiled and turned into vinegar! GameOver!!')
          GameService.saveLog(3,Date.now())
        }
        else
        this.showMsg('You Won!')
        GameService.saveLog(2,Date.now())

        break;

      case 'three':
        GameService.roll(3);
        this.showMsg('The dragon ate you! Game Over')
        GameService.saveLog(1,Date.now())

        break;

      case 'four':
        GameService.roll(4);
        GameService.saveLog(2,Date.now())
        this.showMsg('You won!ぉ')

        break;

      case 'five':
        GameService.roll(5);
        let sentences = GameService.getSentences();
        let number = UtilService.getRandomIntInclusive(0, sentences.length)
        let sentence = sentences[number]
        this.showMsg(sentence.description)
        break;

      case 'six':
        GameService.roll(6);
        this.showMsg('You won! You came to the island and survived')
        break;

      default: console.log(this.state.die);
    }
  }


  getRandomNum = () => {
    return (Math.random() * (2 - 1));
  }
  
  showMsg = (msg) => {
    this.setState({ isOpen: true, msg: msg })
  }
  closeModal = () => {
    this.setState({
      isOpen: false,
    });
  };

  render() {
    const handleBtn = this.state.rolling ?
      'RollDice-rolling' : ''
    const { die, rolling, msg, isOpen } = this.state
    return (
      <div className='RollDice'>
        <button className={handleBtn}
          disabled={this.state.rolling || isOpen}
          onClick={this.OnRoll}>
          {this.state.rolling ? 'Rolling' : 'Roll dice'}
        </button>
        <div className='RollDice-container'>
          <Die face={die} rolling={rolling} />
          <Modal msg={msg} isOpen={isOpen} closeModal={this.closeModal} />
        </div>
      </div>
    )
  }
}

export default RollDice